<template lang="html">
    <div class="fleet">
         <el-table
            ref="multipleTable"
            :data="tableData3"
            border
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="handleSelectionChange">
             <el-table-column
            type="selection"
            width="55">
            </el-table-column> 
            <el-table-column
            label="车辆信息"
            width="">
            <template scope="scope">{{ scope.row.msg}}</template>
            </el-table-column>
            <el-table-column
            prop="name"
            label="司机信息"
            width="130">
            <template scope="scope">
              <span>{{scope.row.name}}</span>
              <p>{{scope.row.phone}}</p>
            </template>
            </el-table-column>
            <el-table-column
            prop="address"
            label="定位信息"
            show-overflow-tooltip>
            <template scope="scope">
                <span>{{scope.row.address}}</span> 
               <p>{{scope.row.date}}</p>  
            </template>
            </el-table-column>
            <el-table-column
            prop=""
            label="操作"
            show-overflow-tooltip>
            <template scope="scope">
                <el-button type="text" @click="show=!show">车辆定位</el-button>
                <router-link to="/trajectory" class="color1">历史轨迹</router-link>
                <router-link type="text" to="/amendment-o" class="color">申请修正</router-link>
                <a href="javascript:;" class="cololr2" @click="tableData3.splice(index,1)">删除</a>
            </template>
            </el-table-column>
        </el-table>
        <div class="show" v-if="show" >
            <div class='cont'>
                <div class='close clearfix'>
                    <span>车辆定位</span>
                    <span class='el-icon-close' @click="show=!show"></span>
                </div>
                <div class="tw">
                    <img src='../../assets/car.png'/>
                    <div>
                        <p class='yi'>衣龙魁</p>
                        <p>车牌号：XXXXXXXX</p>
                        <p>联系方式：XXXXXXXXXXXXXXXXX</p>
                    </div>
                </div>
                <div class='zi'>
                    <p> 车辆类型：XXXXXXXXX</p>
                    <p> 车辆长度：XXXXXXXXXXXXXX</p>
                    <p> 身份证号：XXXXXXXXXXXXXXXXXXXXXXXX</p>
                </div>
                <com-header></com-header>
                <el-button type="success" class='succ' @click="show=!show">确定</el-button>
            </div>
        </div>
    </div>
</template>


<script>
// import comHeader from '@/components/comHeader'
// import BMap from 'BMap' 
export default {
        name: 'fleet',
    data () {
      return {
        radio: '1',
        tableData3: [{
          msg: '沪DG5060 厢式车 9.6米 10吨 30方',
          name: '衣龙魁',
          phone:'15064558893',
          address: '山东省烟台市莱阳市G204(龙门西路)',
          date:'2016-11-17 18:52:10'
        },{
          msg: '沪DG5060 厢式车 9.6米 10吨 30方',
          name: '衣龙魁',
          phone:'15064558893',
          address: '山东省烟台市莱阳市G204(龙门西路)',
          date:'2016-11-17 18:52:10'
        },{
          msg: '沪DG5060 厢式车 9.6米 10吨 30方',
          name: '衣龙魁',
          phone:'15064558893',
          address: '山东省烟台市莱阳市G204(龙门西路)',
          date:'2016-11-17 18:52:10'
        }],
        multipleSelection: [],
        show:false,

      };
    },
    methods:{
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        vehicle() {
        this.$alert('衣龙魁', '车辆定位',"", {
          confirmButtonText: '确定',
          
        });
      }
    }
}
</script>


<style lang="less">
    table{
        font-size: 12px;
    }
    .el-table th>.cell {
      color: #8290B3;
    }
    .el-table__row{
      height: 100px;
        p{
          color:#989BAE;
        }
    }
    .el-message-box{
        width: 600px;
    }
    .el-message-box__btns button:nth-child(2){
        background: green;
        border-color: green;
    }
    .el-button--text{
        color: #4663A9;
        font-size: 12px;
    }
    .show{
        width: 100%;
        height: 100%;
        background:rgba(0, 0, 0, 0.4);
        z-index: 2;
        position: fixed;
        top:0;
        left: 0;
        .cont{
            width: 700px;
            height: 500px;
            background: #fff;
            z-index: 5;
            position: absolute;
            left: 50%;
            top: 50%;
            margin-left: -350px;
            margin-top: -250px;
            border-radius: 3px;
            .close{
                height: 20px;
                padding: 15px;
                border-bottom: 1px solid #E9E9E9;
                span:first-child{
                    font-size: 18px;
                    float: left;
                }
                span:last-child{
                    color: #999;
                    float: right;
                }
            }
            .tw{
                height: 80px;
                margin: 15px;
                padding-bottom: 15px;
                border-bottom:2px solid #E6E9F2; 
                img{
                    border-radius: 3px;
                    float: left;
                    padding-right: 15px;
                }
                div{
                    float: left;
                    p{
                        color: #666;
                        margin-bottom: 5px;
                        font-size: 14px;
                    }
                    .yi{
                        color: black;
                        font-weight: bold;
                        font-size: 16px;
                        margin-bottom: 15px;
                    }
                }
            }
            .zi{
                margin: 15px;
                height: 80px;
                p{
                    margin-bottom: 10px;
                    color: #666;
                    font-size: 14px;
                }
            }
            // .map{
            //     border: 1px solid red;
            //     margin: 15px;
            //     height: 300px;
            // }
            .succ{
                float: right;
                margin: 15px;
            }
        }
    }
    .color{
        color: #4663A9;
        border-right: 1px solid #D7D7D9;
        padding-right: 4px;
    }
    .color1{
        color: #4663A9;
        border-left: 1px solid #D7D7D9;
        border-right: 1px solid #D7D7D9;
        padding:0 4px;
    }
    .cololr2{
        color:#B4B4B4;
    }
</style>


