<template>
    <div class="mycar-in">
        <div class="mycar_input_first">
            <div>
                司机姓名
                <el-input v-model="input" placeholder="请输入姓名"></el-input></div>
            <div>
                电话
                <el-input v-model="input" placeholder="请输入电话号"></el-input></div>
            <div>
                车牌号
                <el-input v-model="input" placeholder="请输入车牌号"></el-input></div>
            <div>
                车型
                <el-select v-model="value4" clearable placeholder="选择">
                    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
            </el-select></div>
        </div>
        <div class="mycar_input_first">
            <div>
                期望流向
                <el-select v-model="value4" clearable placeholder="选择区间">
                    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                </el-select>
            </div>
            <div>
                车长
                <el-select v-model="value4" clearable placeholder="选择区间">
                    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                </el-select>
            </div>
            <div>
                位置
                <el-select v-model="value4" clearable placeholder="可多选位置" class="zzz">
                    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                </el-select>
            </div>
            <div> 
                状态
                <el-select v-model="value4" clearable placeholder="全部">
                    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                </el-select>
            </div>
        </div>
        <div class="mycar-btn">
            <el-button type="primary" >查询</el-button>
        </div>
        
    </div>
</template>
<script>
export default {
  data() {
    return {
      options: [{
        value: '选项1',
        label: '黄金糕'
      }, {
        value: '选项2',
        label: '双皮奶'
      }, {
        value: '选项3',
        label: '蚵仔煎'
      }, {
        value: '选项4',
        label: '龙须面'
      }, {
        value: '选项5',
        label: '北京烤鸭'
      }],
      value4: ''
    }
  },
  mounted() {


  }
}
</script>
<style lang='less'>
.mycar_input_first{
    display: flex;
    margin-top: 20px;
    justify-content: space-around;
}
.mycar_input_first>div{
    display: flex;
    align-items: center;
    margin-left: 5px;
    color: #7F90BB;
    font-weight: bold;
}
.mycar-btn{
    text-align: center;
    margin-top: 20px;
}
.mycar_input {
  width: 1344px;
  height: 500px;
  border: 1px solid #E7E8ED;
}
.mycar_input div {
  color: #8293c2;
  /* margin-top: 40px; */
}


.el-input {

    justify-content: space-between;
    width: 150px;
    margin-left: 5px;
}


.el-input__inner {
  width: 150px;
}
.zzz{
    margin-left: 12px;
}

.el-button--primary {
  width: 200px;
  background: #2e54a8;
}
</style>




