<template>
<div class="macar-btn">
	<div class="macar-btn-top">
	
<span>操作功能 :</span>
<router-link tag="a" :to="{path:'/vehicle'}">新增车辆</router-link>
<a type="text" @click="open">发送短信</a>
<router-link tag="a" :to="{path:'/fleetone'}">车辆分享</router-link>
<router-link tag="a" :to="{path:'/waybill'}">录入运单</router-link>
</div>
</div>

</template>

<script>
export default {
  name: 'macar-btn',
  data () {
    return {
  
    }
  },
  methods: {
    open() {
      const h = this.$createElement;
      this.$msgbox({
        title: '发送短信',
        message: h('div', null, [
          h('p', null, '发送对象: '),
          h('div', { style: 'width: 100%;height:100px;background:#F5F6FB' }, null),
          h('span', { style: 'margin-left: 0' } ,'发送内容'),
          h('span',{style:'float:right'} ,'字数 100 字 条数2条'),
          h('div', { style: 'width: 100%;height:100px;background:#F5F6FB' }, "第发送一条短信，将从您的消费账号中扣除0.1元;每条短信限57个字,超出部分将由系统自动转记入下一条."),
            h('p', null, '说明'),
            h('p', null, '1、第发送一条短信,将从您的消费账户中扣除0.1元 '),
            h('p', null, '2、每条短信限57个字，超出部分将由系统自动转记下一条。 '),
        ]),
        showCancelButton: true,
        confirmButtonText: '发送',
        
      
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="css">
.macar-btn{

}
.macar-btn-top span{
	color: #959EC9;
	font-weight: bold;
}
.macar-btn a{
	
		display: inline-block;
		width: 90px;
		
		height: 30px;
		text-align: center;
		line-height: 30px;
		text-decoration: none;
		color: white;
		background: #66CC9D;
		margin-left: 20px;
		border-radius: 2px;
	
	
	}
</style>



